import { subscribeToResult } from 'rxjs/internal-compatibility';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort<T>(items: T[], column: string, direction: string): T[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(criterio: any, grupo: string, text: string, pipe: PipeTransform) {
  const term = text.toLowerCase();
  let item: CriterioRemuneracao = criterio as CriterioRemuneracao;
  return item.codigoGrupo.includes(grupo)
    && (item.codigoCriterioRemuneracao.toLowerCase().includes(term)
      || item.tipoComissao.includes(term)
      || item.dataInicioVigencia.toString().includes(term)
      || item.dataFimVigencia.toString().includes(term)
      || item.faixaInicial.toString().includes(term)
      || item.faixaFinal.toString().includes(term)
      || pipe.transform(item.valorRemuneracao).includes(term));
}

function fullMatch(criterio: any, rowId: string) {
  let item: CriterioRemuneracao = criterio as CriterioRemuneracao;
  return (item.rowId === rowId);
}

function validItemB(criterio: any, rowId: string, codigoGrupo: string, codigoCriterioRemuneracao: string, tipoComissao: string, tipoPagamento: string, dataInicioVigencia: string, dataFimVigencia: string, faixaInicial: number, faixaFinal: number) {
  let item: CriterioRemuneracao = criterio as CriterioRemuneracao;
  if (rowId == null || rowId === "" || rowId != item.rowId) {
    if (dataInicioVigencia.length == 9 && dataFimVigencia.length == 9) {
      if (item.codigoGrupo === codigoGrupo) {
        if (item.codigoCriterioRemuneracao === codigoCriterioRemuneracao) {
          if (item.tipoComissao === tipoComissao) {
            if (item.tipoPagamento === tipoPagamento) {
              if (((faixaFinal >= item.faixaInicial) && (faixaFinal <= item.faixaFinal)) || ((faixaInicial >= item.faixaInicial) && (faixaInicial <= item.faixaFinal)) || ((faixaFinal >= item.faixaFinal) && (faixaInicial <= item.faixaInicial))) {
                return item
              }
            }
          }
        }
      }
    }
    if (dataInicioVigencia.length == 10 && dataFimVigencia.length == 9) {
      if (item.codigoGrupo === codigoGrupo) {
        if (item.codigoCriterioRemuneracao === codigoCriterioRemuneracao) {
          if (item.tipoComissao === tipoComissao) {
            if (item.tipoPagamento === tipoPagamento) {
              if (((faixaFinal >= item.faixaInicial) && (faixaFinal <= item.faixaFinal)) || ((faixaInicial >= item.faixaInicial) && (faixaInicial <= item.faixaFinal)) || ((faixaFinal >= item.faixaFinal) && (faixaInicial <= item.faixaInicial))) {
                return item
              }
            }
          }
        }
      }
    }
    if (dataInicioVigencia.length == 9 && dataFimVigencia.length == 10) {
      if (item.codigoGrupo === codigoGrupo) {
        if (item.codigoCriterioRemuneracao === codigoCriterioRemuneracao) {
          if (item.tipoComissao === tipoComissao) {
            if (item.tipoPagamento === tipoPagamento) {
              if (((faixaFinal >= item.faixaInicial) && (faixaFinal <= item.faixaFinal)) || ((faixaInicial >= item.faixaInicial) && (faixaInicial <= item.faixaFinal)) || ((faixaFinal >= item.faixaFinal) && (faixaInicial <= item.faixaInicial))) {
                return item
              }
            }
          }
        }
      }
    }
    if (dataInicioVigencia.length == 10 && dataFimVigencia.length == 10) {
      return (item.codigoGrupo === codigoGrupo && item.codigoCriterioRemuneracao === codigoCriterioRemuneracao
        && item.tipoComissao === tipoComissao && item.tipoPagamento === tipoPagamento && (((faixaFinal >= item.faixaInicial) && (faixaFinal <= item.faixaFinal)) || ((faixaInicial >= item.faixaInicial) && (faixaInicial <= item.faixaFinal)) || ((faixaFinal >= item.faixaFinal) && (faixaInicial <= item.faixaInicial)))

      )
    }
  }
}
