@Injectable({
  providedIn: 'root'
})
export class CriteriosremService<T>{
  private serviceUrl = '/conppwPadraoC9-war/ppwpadrao/v9/criterios';
  private handleError: HandleError;

  listaItems: Array<T> = [];
  private listaGrupos: Grupo[] = [];
  private _loading$ = new BehaviorSubject<boolean>(true);
  private _search$ = new Subject<void>();
  private _grupos$ = new BehaviorSubject<any[]>([]);
  private _items$ = new BehaviorSubject<T[]>([]);
  private _total$ = new BehaviorSubject<number>(0);
  private _serviceName = "CriteriosRemuneracaoService";

  private _state: State = {
    page: 1,
    pageSize: 10,
    codigo: '',
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  constructor(private http: HttpClient,
    private httpErrorHandler: HttpErrorHandler,
    private toastr: ToastrService,
    private pipe: DecimalPipe) {
    this.handleError = httpErrorHandler.createHandleError(this._serviceName);
    this.loadGrupos().subscribe(
      data => {
        this.listaGrupos = data;
        this.listaGrupos.unshift(new Grupo("", "Todos"));
        this._grupos$.next(this.listaGrupos);
      },
      error => console.info(error) // error path
    );
    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._search()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._items$.next(result.items);
      this._total$.next(result.total);
    });
    this.load("").subscribe(items => {
      this._loading$.next(false);
      this.listaItems = items;
      this._items$.next(items);
      this._search$.next();
    });
  }

  get grupos$() { return this._grupos$.asObservable(); }
  get items$() { return this._items$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }
  set codigo(codigo: string) { this._set({ codigo }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this._search$.next();
  }

  private _search(): Observable<SearchResult<T>> {
    const { sortColumn, sortDirection, pageSize, page, searchTerm, codigo } = this._state;

    // 1. sort
    let items = sort(this.listaItems, sortColumn, sortDirection);

    // 2. filter
    items = items.filter(item => matches(item, codigo, searchTerm, this.pipe));
    const total = items.length;

    // 3. paginate
    items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);

    //this.messageService.add("success", this._serviceName + ": Lista de items selecionados");

    return of({ items, total });

  }

  getResourceName(): string {
    return 'external-criteriosrem';
  }

  validItem(codigoGrupo: string, rowId: string, codigoCriterioRemuneracao: string, tipoComissao: string, tipoPagamento: string, dataInicioVigencia: string, dataFimVigencia: string, faixaInicial: number, faixaFinal: number) {
    let items = this.listaItems.filter(item => validItemB(item,rowId, codigoGrupo, codigoCriterioRemuneracao, tipoComissao, tipoPagamento, dataInicioVigencia, dataFimVigencia, faixaInicial, faixaFinal));
    return items.length;
  }

  selectItem(rowid: string): Observable<T> {
    if (rowid === null) return of(null);
    let items = this.listaItems.filter(item => fullMatch(item, rowid));
    return of(items[0]);
  }

  loadGrupos(): Observable<Grupo[]> {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<Grupo[]>(this.serviceUrl + "/grupos")
      .pipe(
        tap(data => this.log(`${this._serviceName}/loadGrupos`, data)),
        catchError(this.handleError<Grupo[]>('Carregar grupos', []))
      );
  }

  load(codigoGrupo: string): Observable<T[]> {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<T[]>(`${this.serviceUrl}/remuneracao/${codigoGrupo}`)
      .pipe(
        tap(
          data => this.log(`${this._serviceName}/load`, data),
          error => {
            console.log('Error')
            this.logError(`${this._serviceName}/load`, error)
          }
        ),
        catchError(this.handleError<T[]>('Carregar itens', []))
      );
  }

  findById(codigoGrupo: string, codigoItem: string): Observable<T> {
    return this.http.get<T>(`${this.serviceUrl}/remuneracao/${codigoGrupo}/${codigoItem}`)
      .pipe(
        tap(
          data => {
            console.log(`${this._serviceName}: Item selecionado com cÃ³digo=${codigoGrupo}/${codigoItem}`)
          },
          error => {
            console.log('Error')
            this.logError(`${this._serviceName}/getItems`, error)
          }
        ),
        catchError(this.handleError<T>('Buscar por ID'))
      );
  }

  include(item: T): Observable<T> {
    return this.http.put<T>(`${this.serviceUrl}`, item, httpOptions).pipe(
      tap(
        data => {
          if (data === null) {
            throwError(`Ocorreu um erro durante a incluisÃ£o do item cÃ³digo=${item['codigoGrupo']}/${item['codigoCriterioRemuneracao']}.`);
          } else {
            let msg = `CritÃ©rio incluido com sucesso!`;
            this.toastr.success(msg, "CyberMessenger");

            //this.listaItems.push(item);
            this.listaItems.unshift(data);
            this._items$.next(this.listaItems);
          }
        }
      ),
      catchError(this.handleError<T>('Incluir'))
      //`Erro durante a inclusÃ£o do item cÃ³digo=${item['rowId']}`
      //this.errors = 'Ocorreu um erro ao tentar guardar o critÃ©rio!';
    );
  }

  save(items: T[]): Observable<T[]> {
    return this.http.post(this.serviceUrl, items, httpOptions).pipe(
      tap(
        (changedList: any) => {
          if (changedList == null) {
            throwError('Ocorreu um erro durante a atualizaÃ§Ã£o do item.');
          } else {
            console.log(`${this._serviceName}: Foram atualizados ${items.length} items com sucesso!`);
            let msg = `Item alterado com sucesso!`;
            this.toastr.success(msg, "CyberMessenger");
            this._items$.next(this.listaItems);
            this._search$.next();
          }
        }
      ),
      catchError(this.handleError<T[]>('Salvar'))
    );
  }

  /*saveOne(item: T): Observable<T> {
    return this.http.post<T>(this.serviceUrl, item, httpOptions).pipe(
      tap((changedList: T[]) => {
        console.log(`${this._serviceName}: Item  ${item.id} Foi atualizado com sucesso!`);
        this.listaItems = changedList;
        this._items$.next(this.listaItems);
        this._search$.next();
      }),
      catchError(this.handleError<T>(`postItems cÃ³digo=${item.id}`))
    );
  }*/

  /** DELETE: delete item from the server */
  remove(rowId: string): Observable<T> {
    //const id = typeof item === 'number' ? item : item[keyName];

    return this.http.delete<T>(`${this.serviceUrl}/${encodeURIComponent(rowId)}`,
    {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
    }
    ).pipe(
      tap(
        _ => {
          let itemIndex = this.listaItems.findIndex((item) => item['rowid'] === rowId);
          if (itemIndex >= 0) {
            this.listaItems.splice(itemIndex, 1);
            console.log(`Item [${rowId}] removido com sucesso`);
          }
          let msg = `Item removido com sucesso!`;
          this.toastr.success(msg, "CyberMessenger");
          this._items$.next(this.listaItems);
        }
      ),
      catchError(this.handleError<T>('Remover'))
    );
  }

  filter(term: string, grupo: string): Observable<T[]> {
    if (!term.trim()) {
      // if not search term, return empty hero array.
      return this.load(grupo);
    }
    //const url = `${this.httpUrl}/${this.serviceUrl}/?term=${term}`;
    const url = `${this.serviceUrl}/${grupo}/${term}`;
    return this.http.get<T[]>(url).pipe(
      tap(_ => console.log(_)),
      catchError(this.handleError<T[]>('Filtrar', []))
    );
    //tap(_ =>console.log(`found items matching "${term}"`)),
  }

  /**
     * Handle Http operation that succeded.
     * @param operation - name of the operation that succeded
     * @param result - Result value to return as the observable
     */
  private log<T>(operation: string, result: T) {
    // TODO: better job of transforming error for user consumption
    console.log(`${operation} succeded`);

    // Let the app keep running by returning an empty result.
    //return of(result as T);
    return result;
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private logError(operation: string, result: HttpErrorResponse) {
    // TODO: send the error to remote logging infrastructure
    if (result.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', result.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${result.status}, ` +
        `body was: ${result.error}`);
    }

    // TODO: better job of transforming error for user consumption
    console.log(`${operation} failed: ${result.message}`);

    // Let the app keep running by returning an empty result.
    //return of(result as T);
    return throwError(
      'Something bad happened; please try again later.');
  }

}
