import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { NgbDateStruct, NgbDate, NgbDatepickerI18n, NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';

import { Observable, of } from 'rxjs';
import { switchMap } from 'rxjs/operators';

import { LovValsService } from '../services/lovvals.service';

import { CriterioRemuneracao } from '../models/criteriosrem.model';
import { LovVals } from '../models/lovvals.model';
import { faCalendarAlt } from '@fortawesome/free-solid-svg-icons';

import { I18n, CustomDatepicker } from '../util/CustomDatepicker';
import { BrDateFormatter } from '../util/DateFormatters';
import { CriteriosremService } from '../services/criteriosrem.service';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { DecimalPipe } from '@angular/common';

@Component({
  selector: 'criteriosrem-edit',
  templateUrl: './criteriosrem-edit.component.html',
  styleUrls: ['./criteriosrem-edit.component.scss'],
  providers: [
    I18n, { provide: NgbDatepickerI18n, useClass: CustomDatepicker },
    { provide: NgbDateParserFormatter, useClass: BrDateFormatter },
    LovValsService, DecimalPipe ]
})
export class CriteriosremEditComponent implements OnInit {
  subtitle: string;
  resourceId: string;
  openType: string;

  goBackLink: string;
  content:CriterioRemuneracao;
  originalCopy:CriterioRemuneracao;

  dataInvalida:boolean = false;
  errors: string;

  modelDataIni: NgbDateStruct;
  modelDataFim: NgbDateStruct;

  tiposComissao$: Observable<any[]>;
  tiposPagamento$: Observable<any[]>;
  static LOCALE_KEY = 'currentLocale';

  contentForm = new FormGroup({
    "codigoGrupo": new FormControl('', [
			Validators.required,
      Validators.maxLength(1)
    ]),
    "codigoCriterioRemuneracao": new FormControl('', [
      Validators.required,
      Validators.maxLength(4)
    ]),
    "tipoComissao": new FormControl('', [
      Validators.required,
      Validators.maxLength(2)
    ]),
    "valorRemuneracao": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "dataInicioVigencia": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "dataFimVigencia": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "faixaInicial": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "faixaFinal": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "rowId": new FormControl('')
  });

  faCalendarAlt = faCalendarAlt;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private lovValsService: LovValsService<LovVals>,
              private criteriosremService: CriteriosremService<CriterioRemuneracao>,
              private toastr: ToastrService,
              public translate: TranslateService) {
    translate.setDefaultLang('pt');
    const currentLocale =  sessionStorage.getItem(CriteriosremEditComponent.LOCALE_KEY);

    translate.setDefaultLang(currentLocale);
    }

  ngOnInit() {
    this.tiposComissao$ = this.lovValsService.valores$;
    console.log(`Tipo comissao: ${this.tiposComissao$}`);
    this.tiposPagamento$ = this.lovValsService.pagamentos$;
    console.log(`Tipo pagamento: ${this.tiposPagamento$}`);
    this.goBackLink = '/'+ this.criteriosremService.getResourceName();
    this.resourceId = this.route.snapshot.paramMap.get('code');
    this.openType = this.route.snapshot.paramMap.get('openType');
    this.subtitle = 'PPW.CRITERIOSREM.ITEMSUBTITLE.' + this.openType.toUpperCase();

    if (this.resourceId === null)
      this.content = new CriterioRemuneracao();
    else
      this.criteriosremService.selectItem(this.resourceId)
        .subscribe(
          item => this.content = (item===null)?this.content:item);




    this.originalCopy =JSON.parse(JSON.stringify(this.content));
    let arrIni = this.content.dataInicioVigencia.split("/");
    this.modelDataIni = NgbDate.from({ year: Number(arrIni[2]), month: Number(arrIni[1]), day: Number(arrIni[0]) });
    let arrFim = this.content.dataFimVigencia.split("/");
    this.modelDataFim = NgbDate.from({ year: Number(arrFim[2]), month: Number(arrFim[1]), day: Number(arrFim[0]) });
    console.log(this.content.tipoComissao);
    this.contentForm.patchValue(this.content);
    this.errors = '';
    console.log(this.content.dataInicioVigencia.substr(0,3).concat(this.content.dataInicioVigencia.substr(4,6)))
    console.log(`Validador : ${this.content.dataInicioVigencia.includes("0",4)}`);
    console.log(this.content.dataInicioVigencia.length)
  }

  setRemoverTemplate(){

  }

  onDataIniChange(selected: NgbDate){
    this.content.dataInicioVigencia = `${selected.day}/${selected.month}/${selected.year}`;
  }

  onDataFimChange(selected: NgbDate){
      this.content.dataFimVigencia = `${selected.day}/${selected.month}/${selected.year}`;
  }

  save() {
    switch (this.openType) {
      case 'remove':
        this.criteriosremService.remove(this.content.rowId).subscribe(
          (criterio:CriterioRemuneracao) => {
             this.router.navigate([this.goBackLink]);
          }
        );
        break;
      case 'change':
        console.log(this.criteriosremService.validItem(this.content.codigoGrupo,this.content.rowId,this.content.codigoCriterioRemuneracao,this.content.tipoComissao,this.content.tipoPagamento,this.content.dataInicioVigencia,this.content.dataFimVigencia,this.content.faixaInicial,this.content.faixaFinal));
        if(this.content.faixaInicial > this.content.faixaFinal){
          let msg =  `Faixa inicial nÃ£o pode ser maior que a faixa final`;
          this.toastr.error(msg, "Erro:");
        }
       if(this.criteriosremService.validItem(this.content.codigoGrupo,this.content.rowId,this.content.codigoCriterioRemuneracao,this.content.tipoComissao,this.content.tipoPagamento,this.content.dataInicioVigencia,this.content.dataFimVigencia,this.content.faixaInicial,this.content.faixaFinal) === 0 ){
        this.criteriosremService.save([this.content]).subscribe(
              (criterios:CriterioRemuneracao[]) => {
                if(criterios != null) this.router.navigate([this.goBackLink]);
              }
        );

      }
      else{
        let msg =  `Faixa sobreposta em um periodo vigente`;
        this.toastr.error(msg, "Erro:");
      }
        break;

      case 'create':
       if(this.content.faixaInicial > this.content.faixaFinal){
          let msg =  `Faixa inicial nÃ£o pode ser maior que a faixa final`;
          this.toastr.error(msg, "Erro:");
       }
       if(this.criteriosremService.validItem(this.content.codigoGrupo,this.content.rowId,this.content.codigoCriterioRemuneracao,this.content.tipoComissao,this.content.tipoPagamento,this.content.dataInicioVigencia,this.content.dataFimVigencia,this.content.faixaInicial,this.content.faixaFinal) === 0){
          this.criteriosremService.include(this.content).subscribe(
              (criterio:CriterioRemuneracao) => {
                if(criterio != null)this.router.navigate([this.goBackLink]);
              }
          );
        }
        else{
          let msg =  `Faixa sobreposta em um periodo vigente`;
          this.toastr.error(msg, "Erro:");
        }
        break;

      default:
        break;
    }
  }
  cancel(){
    this.content = this.originalCopy;
    //let contentId = this.content ? this.content.codigoCriterioRemuneracao : null;
    this.router.navigate([this.goBackLink]);
  }

	get diagnostic() { return JSON.stringify(this.content);}

}
