//import { BrowserModule } from '@angular/platform-browser';
import { CommonModule, DecimalPipe } from '@angular/common';
import { NgModule } from '@angular/core';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HttpClient }    from '@angular/common/http';
import { TranslateModule, TranslateLoader, MissingTranslationHandler } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { NgbDropdownModule, NgbDatepickerModule, NgbPaginationModule, NgbTypeaheadModule } from '@ng-bootstrap/ng-bootstrap';
import { ToastrModule } from 'ngx-toastr';

import { CriteriosremListComponent } from './list/criteriosrem-list.component';
import { CriteriosremEditComponent }  from './editor/criteriosrem-edit.component';

import { NgbdSortableHeader } from './directives/sortable.directive';
import { CriteriosremService } from './services/criteriosrem.service';
import { CriteriosremRoutingModule } from './criteriosrem-routing.module';
import { PPWMissingTranslationHandler } from './util/missingTranslationHandler';
import { CriteriosremComponent } from './criteriosrem.component';
import { HttpErrorHandler } from './services/http-error-handler.service';

const localeStartingURL = '/conppwPadraoC9-war/assets/i18n/';
const localeFileType = ".json";

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, localeStartingURL, localeFileType);
}

@NgModule({
  declarations: [
    CriteriosremComponent,
     CriteriosremListComponent,
	   CriteriosremEditComponent,
     NgbdSortableHeader 
  ],
  imports: [
    CommonModule,
    FontAwesomeModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    //NgbModule,
    NgbDropdownModule,
    NgbDatepickerModule,
    NgbPaginationModule, 
    NgbTypeaheadModule,
    ToastrModule.forRoot() ,
    TranslateModule.forChild({
          missingTranslationHandler: {provide: MissingTranslationHandler, useClass: PPWMissingTranslationHandler},
          loader: { provide: TranslateLoader, useFactory: HttpLoaderFactory, deps: [HttpClient]},
            isolate: true
        }),
    CriteriosremRoutingModule
  ],
  providers: [HttpErrorHandler, CriteriosremService, DecimalPipe],
  bootstrap: [CriteriosremComponent]
})
export class CriteriosremModule { }
