import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CriteriosremComponent } from './criteriosrem.component';
import { CriteriosremListComponent } from './list/criteriosrem-list.component';
import { CriteriosremEditComponent } from './editor/criteriosrem-edit.component';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: CriteriosremComponent,
          children: [
            {path: '', component: CriteriosremListComponent},
            {path: ':openType',component: CriteriosremEditComponent},
            {path: ':openType/:code',component: CriteriosremEditComponent }
          ]
        }
    ])
  ],
  exports: [RouterModule]
})
export class CriteriosremRoutingModule { }
