import { Component, OnInit, Input, QueryList, ViewChildren } from '@angular/core';

import { DecimalPipe } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

import { Observable } from 'rxjs';

import { CriteriosremService } from '../services/criteriosrem.service';

import { CriterioRemuneracao } from '../models/criteriosrem.model';

import { NgbdSortableHeader, SortEvent } from '../directives/sortable.directive';
import { faSort, faPlus, faTrashAlt, faPencilAlt, faSearch, faTimesCircle, faPaperPlane } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'criteriosrem-list',
  templateUrl: './criteriosrem-list.component.html',
  styleUrls: ['./criteriosrem-list.component.scss'],
  providers: [CriteriosremService, DecimalPipe]
})
export class CriteriosremListComponent implements OnInit {
  title = '';
  resourceName = '/';
  faPlus = faPlus;
  faPencilAlt = faPencilAlt;
  faTrashAlt = faTrashAlt;
  faSearch = faSearch;
  faSort = faSort;
  grupos$: Observable<any[]>;
  loading$: Observable<boolean>;
  criteriosRem$: Observable<CriterioRemuneracao[]>;
  total$: Observable<number>;

  selectedGrupo: string = "";
  selectedId: string = "";
  static LOCALE_KEY = 'currentLocale';


  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  constructor(private route: ActivatedRoute,
              private router: Router,
              private criteriosremService: CriteriosremService<CriterioRemuneracao>,
              public translate: TranslateService) {
    this.grupos$ = criteriosremService.grupos$;
    this.criteriosRem$ = criteriosremService.items$;
    this.total$ = criteriosremService.total$;
    this.loading$ = criteriosremService.loading$;
    this.resourceName = '/'+ criteriosremService.getResourceName();

    translate.setDefaultLang('pt');
    const currentLocale =  sessionStorage.getItem(CriteriosremListComponent.LOCALE_KEY);

    translate.setDefaultLang(currentLocale);

  }

  get page() { return this.criteriosremService.page; }
  get pageSize() { return this.criteriosremService.pageSize; }
  get searchTerm() { return this.criteriosremService.searchTerm; }
  set page(page: number) { this.criteriosremService.page =  page;}
  set pageSize(pageSize: number) { this.criteriosremService.pageSize =  pageSize; }
  set searchTerm(searchTerm: string) {  this.criteriosremService.searchTerm = searchTerm; }

  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting items
    this.criteriosremService.sortColumn = column;
    this.criteriosremService.sortDirection = direction;
  }

  ngOnInit() {

  }

  create(): void {
    this.router.navigate([this.resourceName, 'create']);
  }

  change(indexId:string): void {
    this.router.navigate([this.resourceName, 'change', indexId]);
  }

  remove(indexId:string): void {
    this.router.navigate([this.resourceName, 'remove', indexId]);
  }

  clear(): void {
    this.searchTerm = "";
    this.headers.forEach(header => {
        header.direction = '';
    });
  }

  selecionaGrupo(grupo: any) {
     this.selectedGrupo = grupo.codigoGrupoCriterio;
     this.criteriosremService.codigo = grupo.codigoGrupoCriterio;
  }
}
